import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

export default function DashboardPage() {
  const cards = [
    { label: "สินค้าทั้งหมด", value: "120" },
    { label: "ใกล้หมด", value: "8", danger: true },
    { label: "รับเข้า (วันนี้)", value: "+32" },
    { label: "เบิกออก (วันนี้)", value: "-17" },
  ];

  return (
    <div className="space-y-6">
      <div className="flex items-end justify-between">
        <div>
          <h1 className="text-2xl font-bold">Dashboard</h1>
          <p className="text-sm text-slate-500">ภาพรวมสต็อกสินค้า</p>
        </div>

        <Badge variant="secondary">Demo UI</Badge>
      </div>

      <div className="grid grid-cols-1 gap-4 md:grid-cols-4">
        {cards.map((c) => (
          <Card key={c.label} className="rounded-2xl">
            <CardContent className="p-4">
              <div className="text-sm text-slate-500">{c.label}</div>
              <div
                className={[
                  "mt-1 text-2xl font-bold",
                  c.danger ? "text-red-600" : "text-slate-900",
                ].join(" ")}
              >
                {c.value}
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
        <Card className="rounded-2xl">
          <CardContent className="p-4">
            <div className="font-semibold">สินค้าที่ใกล้หมด</div>
            <p className="mt-1 text-sm text-slate-500">
              แนะนำให้ตั้งค่า “จุดสั่งซื้อขั้นต่ำ” ในหน้า “สินค้า”
            </p>

            <ul className="mt-4 space-y-2 text-sm">
              <li className="flex items-center justify-between rounded-xl bg-white p-3">
                <span>น้ำมันพืช</span>
                <span className="font-semibold text-red-600">เหลือ 4</span>
              </li>
              <li className="flex items-center justify-between rounded-xl bg-white p-3">
                <span>ซีอิ๊วขาว</span>
                <span className="font-semibold text-red-600">เหลือ 2</span>
              </li>
            </ul>
          </CardContent>
        </Card>

        <Card className="rounded-2xl">
          <CardContent className="p-4">
            <div className="font-semibold">กิจกรรมล่าสุด</div>
            <p className="mt-1 text-sm text-slate-500">การเคลื่อนไหวรับเข้า/เบิกออก</p>

            <ul className="mt-4 space-y-2 text-sm">
              <li className="rounded-xl bg-white p-3">
                รับเข้า น้ำปลา +20 (วันนี้)
              </li>
              <li className="rounded-xl bg-white p-3">
                เบิกออก น้ำมันพืช -3 (วันนี้)
              </li>
              <li className="rounded-xl bg-white p-3">
                รับเข้า ซีอิ๊วขาว +10 (เมื่อวาน)
              </li>
            </ul>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
